*{
    margin: 0;
    padding: 0;
    box-sizing: border-box;
}

.nav-list{
    height: 10vh;
    display: flex;
    width: 50%;
    justify-content: space-around;
    align-items: center;
    margin-left: auto;
    list-style: none;
    padding-bottom: 10px;
}

.nav-links{
    text-decoration: none;
    color: black;
    font-size: 18px;
    font-family: 'Press Start 2P', cursive;
}
.contact,
.about,
.home{
    height: 90vh;
    position: absolute;
    width: 100%;
}

.contact-content,
.about-content,
.home-content{
    height: 100%;
    display: flex;
    justify-content: space-evenly;
    align-items: center;
    font-family: 'Press Start 2P', cursive;
    img{
        height: 70%;
    }
    h1{
        font-size: 50px;
        margin: 30px, 0px;
    }
    p {
        font-size: 30px;
    }
}

.home{
    background-image: linear-gradient(135deg, #f5f7fa 0%, #c3cfe2 100%);;
}
.about{
    background-image: linear-gradient(to right, #ffecd2 0%, #fcb69f 100%);
}
.contact{
    background-image: linear-gradient(to top, #dad4ec 0%, #dad4ec 1%, #f3e7e9 100%);
}